<template>
  <div id="haut" class="blockHeroscreen">
    <picture class="blockHeroscreen-image">
      <img class="active" src="../assets/images/stage.jpg" alt="image">
    </picture>
    <blocBaseline/>
  </div>
</template>

<style lang="scss" scoped>

  .blockHeroscreen{
    width: 100%;
    height: calc(100vh - 100px);
    &-image > img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &-link{
      position: absolute;
      cursor: pointer;
      border: none;
      left: 50%;
      bottom: 0;
      border-radius: 10px;
      padding: 20px;
      transform: translateX(-50%);
      color: $dark;
      font-size: 1.2em;
      margin-bottom: 20px;
      transition: $trans;
      overflow: hidden;

      &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0;
        transition: $trans;
        z-index: -1;
      }

      &:hover{
        color: $col1;
        &::before{
          height: 100%;
        }
      }

    }
  }

</style>

<script>

  import blocBaseline from '@/components/blocBaseline.vue'
  export default {
    name: 'blocHeroscreen',
    components: {
      blocBaseline
    },
    data(){
      return{
      }
    },
    methods:{

    }
  }
</script>